import { CustomSelect } from '@/components/dropDown/Dropdown';
import CloseIcon from '@/components/icons/CloseIcon';
import { useEffect, useState } from 'react';
import useCarFilters from '../../hooks/useCarFilters';
import { carsTypes } from '../../data/configs.data';
import { Brand } from '@/types/cars.types';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';

interface Props {
    setFilters: any;
}

const CarListFilters = ({ setFilters }: Props) => {
    const { filters, updateFilter, resetFilters } = useCarFilters();
    const [brands, setBrands] = useState<Brand[]>([]);

    useEffect(() => {
        const fetchBrands = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.brands.list });
                setBrands(response.data.data);
            } catch (error) {
                //handle error
            }
        };

        fetchBrands();
    }, []);

    useEffect(() => {
        setFilters(filters);
    }, [filters]);

    const types = carsTypes.map((el) => {
        return { label: el, value: el };
    });

    const carBrands = brands.map((el) => {
        return {
            label: el.name,
            value: String(el.id),
        };
    });

    return (
        <div className="flex flex-col xl:flex-row items-center justify-between">
            <CustomSelect
                value={filters.vehicle_brand_id || ''}
                onValueChange={(value) => updateFilter('vehicle_brand_id', value)}
                placeholder="ماركة السيارة"
                width="min-w-[228.25px]"
                options={carBrands}
            />
            <CustomSelect
                value={filters.car_type || ''}
                onValueChange={(value) => updateFilter('car_type', value)}
                placeholder="النوع"
                width="min-w-[228.25px]"
                options={types}
            />
            <CustomSelect
                value={filters.status || ''}
                onValueChange={(value) => updateFilter('status', value)}
                placeholder="حالة التوفر"
                width="min-w-[228.25px]"
                options={[
                    { label: 'متاحه', value: 'available' },
                    { label: 'غير متاحه', value: 'not available' },
                ]}
            />
            <CustomSelect
                value={filters.branch || ''}
                onValueChange={(value) => updateFilter('branch', value)}
                placeholder="الفرع"
                width="min-w-[228.25px]"
                options={[{ label: 'option1', value: 'option1' }]}
            />
            <button
                onClick={resetFilters}
                className="flex flex-row items-center gap-2 h-[22px] p-0 bg-white-blue-50 hover:bg-white-blue-200 border-none shadow-none text-primery-blue-500 font-medium text-xs leading-[22.49px] tracking-normal"
            >
                <CloseIcon className="!w-[10px] !h-[10px]" />
                إلغاء التصفية
            </button>
        </div>
    );
};

export default CarListFilters;
