import { Car } from "@/types/cars.types";

interface Props {
    cars: Car[];
    CarCard: any;
}

const CarListComponent = ({ cars, CarCard }: Props) => {
    return (
        <>
            <div className="grid custom-grid-car-image gap-6">
                {cars.map((car) => (
                    <div key={car.id} className="">
                        <CarCard car={car} />
                    </div>
                ))}
            </div>
            {cars.length < 1 && <div className="mx-auto text-center">لا يوجد نتائج لعرضها</div>}
        </>
    );
};

export default CarListComponent;
