import { useState, useCallback } from 'react';

interface CarFilters {
    vehicle_brand_id?: string;
    car_type?: string;
    status?: string;
    branch?: string;
}

interface UseCarFiltersReturn {
    filters: CarFilters;
    updateFilter: (filterName: keyof CarFilters, value: string) => void;
    resetFilters: () => void;
}

const useCarFilters = (): UseCarFiltersReturn => {
    const [filters, setFilters] = useState<CarFilters>({});

    const updateFilter = useCallback((filterName: keyof CarFilters, value: string) => {
        setFilters((prev) => ({ ...prev, [filterName]: value }));
    }, []);

    const resetFilters = useCallback(() => {
        setFilters({});
    }, []);

    return {
        filters,
        updateFilter,
        resetFilters,
    };
};

export default useCarFilters;
