import { CustomPagination } from './CustomPagination';

interface Props {
    currentPage: number;
    setCurrentPage: any;
    totalItems: number;
}

const CarListPagination = ({ currentPage, setCurrentPage, totalItems }: Props) => {
    const itemsPerPage = 10;

    return (
        <div className="flex flex-row items-center justify-between px-4 py-3 border-t bordr-[#E1DEE0]">
            <p className="font-normal text-primery-blue-600 text-base leading-7 tracking-normal">
                عرض {(currentPage - 1) * itemsPerPage + 1} الى
                {itemsPerPage * currentPage > totalItems ? totalItems : itemsPerPage * currentPage} من
                {totalItems}
            </p>
            <div>
                <CustomPagination
                    totalItems={totalItems}
                    itemsPerPage={itemsPerPage}
                    currentPage={currentPage}
                    onPageChange={(page) => setCurrentPage(page)}
                />
            </div>
        </div>
    );
};

export default CarListPagination;
